     font-size: $font-size-big;\n                    color: $white-warm;\n                    margin: auto;\n                    padding: 0;\n                    cursor: pointer;\n                    transition: color 0.3s ease-in-out;\n\n                    > i {\n                        transform: rotate(0);\n                        color: inherit;\n                        padding: 0 5px;\n                    }\n\n                    &:hover {\n                        color: $white-light;\n                    }\n\n                    &.disabled{\n                        cursor: not-allowed;\n                        color: $green-light;\n                    }\n\n                    &.selected {\n                        color: $white-light;\n\n                        > i {\n                            transform: rotate(180deg);\n                        }\n                    }\n                }\n            }\n\n            > nav{\n                flex-grow: 1;\n                padding: 0;\n                margin: 0;\n                max-width: 30%;\n                min-width: 30%;\n                min-height: $taskbar-height;\n                max-height: $taskbar-height;\n\n                &.views {\n                    display: flex;\n\n                    > ul {\n                        justify-content: flex-end;\n                        flex-grow: 0;\n\n                        &.actions {\n                            flex-grow: 2;\n                        }\n                    }\n                }\n\n                > ul{\n                    flex-grow: 1;\n                    padding: 0;\n                    margin: 0;\n                    list-style: none;\n                    height: $taskbar-height;\n                    display: flex;\n                    flex-direction: row;\n                    align-items: center;\n                    padding: 0 5px;\n\n                    > li{\n                        margin: auto 5px auto 0;\n                        padding: 5px 10px;\n                        color: $white-warm;\n                        transition: color 0.3s ease-in-out;\n                        cursor: pointer;\n\n                        &.tabs {\n                            height: $taskbar-height;\n                            line-height: $taskbar-line-height;\n                            text-transform: uppercase;\n                            border-bottom: 2px solid transparent;\n                            transition: color 0.3s ease-in-out, border-bottom-color 0.3s ease-in-out;\n                        }\n\n                        > i{\n                            font-size: $icon-size-default;\n                        }\n\n                        &.option-button {\n                            display: flex;\n                            flex-direction: row;\n                            position: relative;\n\n                            >aside {\n                                margin-left: 5px;\n                                padding: 0 2px;\n                                flex-grow: 1;\n                                background-color: transparent;\n                                transition: background-color 0.3s ease-in-out;\n\n                                &:hover {\n                                    background-color: $white-warm;\n                                }\n                            }\n\n                            > .action-panel {\n                                position: absolute;\n                                top: $taskbar-height - 5px;\n                                right: 0;\n                                width: 240px;\n                                min-width: 220px;\n                                width: 100%;\n                                background-color: $white-warm;\n                    \n                                @include box-shadow(bottom, 0.5);\n                            }\n                        }\n\n                        &.separator {\n                            padding: 0;\n                            height: $taskbar-height - 16px;\n                            width: 1px;\n                            margin: auto 0;\n                            background-color: $green-light;\n                        }\n\n                        &:hover{\n                            color: $white-light;\n                        }\n\n                        &.disabled{\n                            cursor: not-allowed;\n                            color: $green-light;\n                        }\n\n                        &.selected {\n                            border-bottom-color: $white-light;\n                            color: $white-light;\n                        }\n                    } \n                }\n            }\n        }\n\n        > #attributes {\n            margin: 15px 50% auto;\n            position: absolute;\n            top: 100%;\n            left: auto;\n            bottom: auto;\n            right: auto;\n            transform: translateX(-50%);\n            max-width: 600px;\n            min-width: 220px;\n            width: 100%;\n            z-index: -1;\n            @include box-shadow(bottom, 0.5);\n        }\n    }\n\n    &.embebed {\n        > header {\n            > div{\n                background-color: $white-ximdex;\n\n                > .title {\n                    > h2 {\n                        color: $white-darker;\n\n                        &:hover{\n                            color: $black-light;\n                        }\n\n                        &.disabled{\n                            color: $white-dark;\n                        }\n\n                        &.selected {\n                            color: $black-light;\n                        }\n                    }\n                }\n\n                > nav {\n                    >ul {\n                        > li {\n                            color: $white-darker;\n\n                            &.separator {\n                                background-color: $white-dark;\n                            }\n\n                            &:hover{\n                                color: $black-light;\n                            }\n\n                            &.disabled{\n                                color: $white-dark;\n                            }\n        \n                            &.selected {\n                                border-bottom-color: $black-light;\n                                color: $black-light;\n                            }\n                        }\n                    }\n                }\n            }\n        }\n    }\n}","@mixin box-shadow ($pos, $opacity, $important: false) {\n    $_pos: 0px 0px 3px 0px ;\n    @if($pos == top){\n        $_pos: 0px 3px 11px 0px ;\n    }@else if($pos == left) {\n        $_pos: -2px 0px 11px -4px;\n    }\n\n    $rgb: rgba(0,0,0,$opacity);\n    @if($important == true){        \n        $rgb: rgba(0,0,0,$opacity) !important;\n    }\n\n    box-shadow: $_pos $rgb;\n}"],"sourceRoot":""}]);

